<template>
  <div class="hello" @click="changePi" @dbclick="changeMode">
    <div class="tip">当前页面(第{{pi+1}}页)</div>
    <TimeCard 
            :img="ppts[pi].src" 
            :text="ppts[pi].text" 
            :index="pi"
            :key="pi+'sad'"
            @click="changePi"> 
    </TimeCard>
  </div>
</template>

<script>
import TimeCard from './widgets/TimeCard';

export default {
  name: 'Home',
  components:{
    TimeCard
  },
  data () {
    return {
      mode:'auto',
      ppts:[{
        src:'../static/img/1.jpg',
        text: ['履不必同，期于适足，治不必同，期于利民。我们就是要坚持走中国特色社会主义道路，坚持共产党的领导，坚持马克思列宁主义，毛泽东思想，邓小平理论，三个代表重要思想，科学发展观。']
      },{
        src:'../static/img/2.jpg',
        text: ['坚持新时代中国特色社会主义思想，提升道路自信，理论自信，制度自信，在中国共产党的领导下，不忘初心，牢记使命，决胜全面建设小康社会，夺取新时代中国特色社会主义伟大胜利，为实现中华民族伟大复兴的中国梦不懈奋斗']
      },{
        src:'../static/img/3.jpg',
        text: ['作为一名军人，我们最重要的职责使命就是为中国特色社会主义伟大实践保驾护航，我们要把听党指挥，维护核心当作最主首要和最关键的政治要求，要树牢敢打仗，敢打硬仗的血性信念，具备能打仗，能打恶战的能力素质。要沉心静气，把所有的精力和心思都集中到完成好本职工作上来，在平凡的岗位中实现不平凡的人生。']
      },{
        src:'../static/img/4.jpg',
        text: ['人生中，面对机会',
            '犹豫者，优柔寡断，瞻前顾后。',
            '观望者，冷眼旁观，踌躇不前。',
            '懈怠者，意志不坚，放任自我。',
            '软弱者，缺乏胆气，魄力不足。',
            '想要做人生赢家，我们要始终做到克服犹豫，观望，懈怠，软弱，把握人生机会，做一个智者，勇者和践行者，为了实现自身梦想而不懈努力奋斗！']
      }],
      pi:0,
      tid:null,
      ppt: null
    }
  },
  methods:{
    changePi() {
      if(this.pi<this.ppts.length-1) {
        this.pi+=1;
      } else {
        this.pi=0;
      }
      console.log(this.pi)
    },
    changeMode() {
      this.mode=(this.mode=='auto'?'notauto':'auto');
    }
  },
  mounted() {
    let tid=setInterval(()=>{
      this.changePi();
    },1000*80)
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.hello {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
}
.tip {
  background: #000;
  color:#fff;
  width:100%;
  padding:10px 0;
  margin-bottom: 12px;
}
.flex {
  display: flex;
}
</style>



