<template>
  <div class="time-card">
    <transition :name="i">
      <img :src="img" alt="" 
            :class="{
              'main-img':true,
              [i]:true
            }" 
            v-if="isShow"
            >
    </transition>
    <ul class="desc">
      <li v-for="txt in desc">{{txt}}</li>
    </ul>
  </div>
</template>

<script>
export default {
  name: 'TimeCard',
  props: [ 'img' , 'text' ,'index' ],
  data () {
    return {
      desc: [],
      isShow:false
    }
  },
  computed: {
    i() {
      return "ani"+this.index
    }
  },
  methods:{
    mapText() {
      var t=this,
          texts=this.text;
      texts.map(text=>{
        let txt=text;
        for(let i = 0 , len=txt.length , j=0 ; i<len;i=i+6,j=j+1) {
          setTimeout(()=>{
            t.desc=[...t.desc.slice(0,-1),txt.slice(0,i+6)];
          },j*80);
        }
        t.desc.push(txt);
      });

    }
  },
  created() {  
    setTimeout(()=>{
      this.mapText();
      this.isShow=true;
    },500);
    
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.time-card {
  width:48%;
}
.main-img {
  width:100%;
  border-top-left-radius: 2px;
  border-top-right-radius: 2px;
  transform:  translate(0,0) rotate(0);
}
.desc {
  //border: 1px solid rgba(0,0,0,.125);
  border-top: 0;
  line-height: 1.6;
  text-align: left;
  border-bottom-left-radius: 2px;
  border-bottom-right-radius: 2px;
  padding: 16px ;
  background: #d7d7d7;
  text-indent: 2em;
}

.ani0-enter-active {
  animation: ani0 .5s ease-in-out;
}
@keyframes ani0 {
  0% {
    transform: translate(-300px,-300px) rotate(720deg);
  }
  100% {
    transform: translate(0,0) rotate(0);
  }
}

.ani1-enter-active {
  animation: ani1 .5s ease-in-out;
}
@keyframes ani1 {
  0% {
    transform: translate(300px,-300px) rotate(720deg);
  }
  100% {
    transform: translate(0,0) rotate(0);
  }
}

.ani2-enter-active {
  animation: ani2 .5s ease-in-out;
}
@keyframes ani2 {
  0% {
    transform: scale(0) ;
  }
  100% {
    transform: scale(1);
  }
}

.ani3-enter-active {
  animation: ani3 .5s ease-in-out;
}
@keyframes ani3 {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@media screen and (max-width: 788px) {
    .time-card {
      width:92%;
    }
}
</style>



